<template>
  <div v-if="dialogOpen" class="dialog-base" @click="closeDialog">
    <dialog open>
      <div class="dialog-X">
        <img src="../../assets/close.svg" @click="closeDialog" />
      </div>
      <span class="helper"></span>
      <img :src="theImg" alt="..." @click.stop="" />
    </dialog>
  </div>
</template>

<script>
export default {
  props: ['theImg','dialogOpen'],
  data () {
    return {
    //   dialogOpen: false
    }
  },
  computed: {},
  methods: {
    closeDialog () {
      this.$emit("close-dialog")
    //   this.dialogOpen = false
      this.$store.state.showDialog = false
      document.documentElement.style.overflow = 'auto'
    }
  }
}
</script>

<style scoped>
.dialog-base {
  display: table-cell;
  position: fixed;
  top: 0vh;
  width: 100vw;
  left: 0px;
  height: 100vh;
  overflow: hidden;
  text-align: center;
  vertical-align: middle;

  z-index: 1001;
}
.dialog-X {
  position: absolute;
  right: 2%;
  top: 2%;
  color: white;
  cursor: pointer;
  width: 2%;
}
dialog {
  width: 100%;
  height: 100%;
  border: none;
  display: table-cell;
  /* padding: 2% 5% 2% 5%; */
  /* padding-top: 2%; */
  vertical-align: middle;
  background-color: rgba(0, 0, 0, 0.36);
}
.helper {
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}
dialog img {
  vertical-align: middle;
}
img {
  max-height: 100%;
  width: auto;
  max-width: 100%;
  height: auto;
}
</style>
